<template>
  <div class="home">
   <div class="home-top-title clearfix">

     <div class="home-logo fl">
       <img class="auto-img" src="../../assets/icon/applogo.png" alt="logo">
     </div>
     <div class="home-logo-name fl">天虎电影院</div>
     <div class="home-sousuo fr">
       <input type="text">
       <van-icon class="home-sousuo-icon" name="search" />
     </div>
   </div>

    <div class="home-Carousel">
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(image, index) in new_movies" :key="index">
          <img class="auto-img" :src="image" />
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="home-List" v-for="(v, i) in List_dataApi" :key="i">
      <h2>{{v.title}}</h2>
      <ul>
        <li v-for="(item, index) in dataIndex[i]" :key="index" @click="Rxiangqin(item.subject == undefined ? item.id : item.subject.id)">
          <div class="home-List-img">
            <img class="auto-img" :src="item.subject == undefined ? item.images.medium : item.subject.images.medium" alt="">
          </div>
          <div class="home-List-name one-text">{{item.subject == undefined ? item.title : item.subject.title}}</div>
        </li>
      </ul>
    </div>


  </div>
</template>

<script>

  export default {
    name: 'home',

    data() {
      return {
        //请求基础路径
          baseUrl: 'https://douban.uieee.com',

          new_movies: [
            'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576318420460&di=eab0d231f48b221644614396bc9b87d0&imgtype=0&src=http%3A%2F%2Fi0.hdslb.com%2Fbfs%2Farticle%2Fe17fea8ffacd9bc977c7ac95bb2dc3ff6dae5de0.jpg',
            'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576318330514&di=710f15aa655fd25fa807bd65aa290bde&imgtype=0&src=http%3A%2F%2F001.img.pu.sohu.com.cn%2Fgroup2%2FM05%2FC0%2FB5%2FMTAuMTAuODguODE%3D%2FdXBsb2FkRmlsZV85XzE0NjMwMjg4Mzk4Njc%3D.jpg',
            'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576318330502&di=f1844880b391b95fd8ca2819ce3085f7&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2Fa%2F5743eb1f535f3.jpg',
            'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576318329887&di=3eee5e68005d3af4735fc8a4fc0f0a3a&imgtype=0&src=http%3A%2F%2Fpic1.win4000.com%2Fwallpaper%2F0%2F5417ca4eabf61.jpg',
            'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1576318330523&di=8fe2139b8310ad9924a74e25c562f936&imgtype=0&src=http%3A%2F%2Fwww.91zhuti.com%2Fuploads%2Fallimg%2F131202%2F4-131202111234.jpg',
          ],

          List_dataApi: [
            {
              title: 'Top250',
              url: '/v2/movie/top250'
            },
            {
              title: '北美票房榜',
              url: '/v2/movie/us_box'
            },
            {
              title: '口碑榜',
              url: '/v2/movie/weekly'
            },
            {
              title: '新片榜',
              url: '/v2/movie/new_movies'
            },

          ],

          dataIndex: [],

      };
    },

    methods: {
      home_List_data(item, index){
        this.axios({
            //请求类型
            method: 'GET',
            //请求地址
            url: this.baseUrl + item,
          }).then(result => {
            // console.log(result.data.subjects)
            this.dataIndex[index] = result.data.subjects;
            this.dataIndex = [...this.dataIndex];
          }).catch(err => {
            console.log('err ==> ', err);
            return 0;
        })
        return 0;
      },

      Rxiangqin(id){
        console.log(id)
        this.$router.push({name: 'movieDetail', params: {id}});
      }

    },
    // 钩子
    created(){
      this.dataIndex = Array(this.List_dataApi.length);
      this.List_dataApi.forEach((v, i) => {
        // console.log(v.url, i)
        this.home_List_data(v.url, i);

      })

      



    }

  }

</script>

<style scoped lang="less">
  .home{
    padding-bottom: 55px;
  }
  .home-List{
    padding: 10px;
    h2{
      border-left: 2px solid #007AFF;
      padding-left: 5px;
      margin: 5px;
    }
    ul{
      white-space: nowrap;
      overflow-x: auto;
      li{
        display: inline-block;
        margin: 10px;
        img{
          border-radius: 10px;
          box-shadow: 2px 2px 5px 1px #999;
        }
        .home-List-name{
          font-size: 16px;
          margin-top: 5px;
          text-indent: 14px;
          width: 3rem;
        }
      }
    }
    .home-List-img{
      width: 3rem;
    }
  }
  .home-Carousel{

  }
  .home-top-title{
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    padding: .2rem;
    box-sizing: border-box;
    z-index: 10;
    background-color: rgba(255,255,255,.8);
    .home-logo{
      width: 1rem;
    }
    .home-logo-name{
      line-height: 1.066rem;
      color: #0293f0;
      margin-left: .2rem;
      font-size: 0.426rem;
      font-weight: bold;
    }
    .home-sousuo{
      width: 50%;
      margin-right: 0.2rem;
      margin-top: 0.18rem;
      position: relative;
      border-radius: 0.4rem;
      box-shadow: 0 0 2px 0 #ff3f7e;
      padding: .1rem;
      background-color: #f2f2f2;
      height: .45rem;
      >input{
        border: none;
        text-indent: .2rem;
        color: #666;
        background: none;
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: .8rem;
      }
      .home-sousuo-icon{
        position: absolute;
        right: 0.266rem;
        font-size: 0.426rem;
        color: #000;
        &:active{
          color: #ff3f7e;
        }
      }

    }
  }
</style>
